import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	data() {
		return {
			userInfo:{},
			avatarUrl:'../../static/images/avater.png',
			token:'',
		};
	},
	computed:{
		value(){
			var birthday = this.userInfo.birthday
			var arr = [];
			if(birthday){
				arr = birthday.split('-')
				arr[0] = arr[0]-1900;
				arr[1] = arr[1]-1;
				arr[2] = arr[2]-1;
			}
			return arr;
			
		}
	},
	filters:{
		sexName(value){
			if(value == 1){
				return '男';
			}else if(value == 2){
				return '女'
			}else{
				return '保密'
			}
		}
	},
	onShow(){
		const value = uni.getStorageSync('TOKEN');
		if (value) {
			this.token = value;
			this.getUserInfo();
		}else{
			this.token = '';
			this.userInfo = {}
		}
	},
	onLoad(e) {
		this.userInfo = JSON.parse(e.info)
		uni.getStorage({
			key: 'TOKEN',
			success: (res)=> {
				this.token = res.data
			}
		});
	},
	methods:{
		getUserInfo(){
			this.$uniRequest(
				this.$baseUrl+'/v1/member/info',
				0,
				(res)=>{
					this.userInfo = res.data.data;
				}
			)
		},
		changeAvatar(){
			uni.chooseImage({
				count: 1, //默认9
				sourceType: ['album', 'camera'], //从相册选择
				sizeType: ['compressed'],
				success: (img)=> {
					var avatar = img.tempFilePaths[0];
					uni.redirectTo({
						url: '/pages/upload/upload?src=' + avatar
					});

				}
			});
		},
		changeName(){
			uni.navigateTo({
				url:'/pages/changeName/changeName?info='+ JSON.stringify(this.userInfo)
			})
		},
		changeBirthday(){
			this.$refs.picker.show();
		},
		changeSign(){
			uni.navigateTo({
				url:'/pages/changeSign/changeSign?info='+ JSON.stringify(this.userInfo)
			})
		},
		changeSex(){
			uni.showActionSheet({
				itemList: ['保密', '男', '女'],
				success: (item)=> {
					this.$uniRequest(
						this.$baseUrl+'/v1/member/edit',
						{
							birthday:this.userInfo.birthday,
							sex:item.tapIndex,
							nickname:this.userInfo.nickname
						},
						(res)=>{
							console.log(res)
							this.userInfo.sex = item.tapIndex;
							uni.showToast({
								title:res.data.msg
							})
						}
					)
				},
				fail: (res)=> {
					uni.showToast({
						title:"未修改成功,请重试",
						icon:'none'
					})
				}
			});
		},
		onConfirm(val){
			this.$uniRequest(
				this.$baseUrl+'/v1/member/edit',
				{
					birthday:val.result,
					sex:this.userInfo.sex,
					nickname:this.userInfo.nickname
				},
				(res)=>{
					this.userInfo.birthday = val.result;
					uni.showToast({
						title:res.data.msg
					})
				}
			)
		}
	},
	components: {
		wPicker
	}
}
